        },
		onLoad() {
			myvue = this;
		},
		
        methods: {
			...mapMutations(['login']), 
			wxlogin(n){
				var that = this;
				
				uni.login({
				  provider: 'weixin',
				  success: function (loginRes) {
				    //获取code
				    let code = loginRes.code;
					uni.getUserInfo({
						provider: "weixin",
						success:function(infoRes){
							//拼接往后台发送的数据
							var data = {
								code: code,
								user_info: infoRes.rawData,
								encrypted_data:infoRes.encryptedData,
								iv: infoRes.iv,
								signature: infoRes.signature
							}
							 // console.log(infoRes.rawData)
							//发起登录请求
							that.$H.post('/mannerdish/user/login',data,{
								// 这里测试先不用token
								token:false,
							}).then((res)=>{
								res.myvue = myvue;
								that.login(res)
								return
								
								setTimeout(function () {
								    uni.hideLoading();
								}, 1000);
